
.page {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2vh;
}

.page-alert {
  width: 90%;
  margin: 2vh 0;
  padding: 5vh 0vh;
  font-size: 2.5vh;
  border-radius: 2vh;
  text-align: center;
  color: var(--text-dark);
  background-color: var(--green);
  font-family: 'MADE Evolve Sans', Helvetica, sans-serif;
}

h1, h2 {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', Arial, sans-serif;
}

h1 {
  font-size: 3vh;
  
}

h2 {
  font-size: 2vh;
}

.page-bt {
  color: var(--text-light);
}